import { Character } from '@/interfaces/characters';
import { Reducer } from 'react';

interface CharacterFiltersState {
  list: Character[];
  search: string;
}

type CharacterFiltersAction = { type: 'SET_SEARCH'; payload: string };

const characterFiltersReducer: Reducer<
  CharacterFiltersState,
  CharacterFiltersAction
> = (state, action) => {
  switch (action.type) {
    case 'SET_SEARCH':
      return {
        ...state,
        search: action.payload,
        list: state.list.filter((v) =>
          v.name.toLowerCase().includes(action.payload.toLowerCase())
        ),
      };
    default:
      return state;
  }
};

export { characterFiltersReducer };
export type { CharacterFiltersState, CharacterFiltersAction };

