'use client';

import { Character } from '@/interfaces/characters';
import CharacterItem from './Character';
import { ChangeEvent, useReducer } from 'react';
import { characterFiltersReducer } from './reducer';

interface CharacterProps {
  data: Character[];
}
export default function Characters({ data }: CharacterProps) {
  const [characterState, characterDispatch] = useReducer(
    characterFiltersReducer,
    { list: data, search: '' }
  );
  const handleChange = (e: ChangeEvent<HTMLInputElement>) => {
    console.log(e.target.value);
    characterDispatch({ type: 'SET_SEARCH', payload: e.target.value });
  };
  return (
    <div>
      <input
        type="search"
        value={characterState.search}
        className="rounded border-2 border-accent bg-white text-primary"
        onChange={handleChange}
      />
      <div className="grid grid-cols-4 gap-3">
        {characterState.list.map((c) => (
          <CharacterItem character={c} key={c.id} />
        ))}
      </div>
    </div>
  );
}

